/*
  ===================================================
  Page setup
  ===================================================
*/

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }


body {
  box-sizing: border-box;
 
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: #333; 
  background-color: #ccc;
}

.error {
  width: 100%;
  margin: 40px 0;
  text-align: center;
  color: #d64d96;
  font-size: 3.5rem;
}


.app {
    text-align: center;
}

.header {
    background-image: -webkit-linear-gradient(left, #7ed56f, #28b485);
    background-image: linear-gradient(to right, #7ed56f, #28b485);
    height: 15vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: #fff;
}

.users-found {
    margin-bottom: 4rem;
} 
  
  
  
  
  


.search {
    margin-top: 2rem;
}
.search__form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-direction: column;
            flex-direction: column;
   
}
    
.search__form-input {
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    font-size: 1.5rem;
    font-family: inherit;
    color: inherit;
    padding: 1.5rem 2rem;
    border-radius: 2px;
    background-color: rgba(#fff, 0.5);
    border: none;
    border-bottom: 3px solid transparent;
    border-radius: 100px;
    width: 50%;
    margin-right: 5rem;
 
    -webkit-transition: all .3s;
 
    transition: all .3s; }
    
.search__form-input:focus {
    outline: none;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
    border-bottom: 3px solid #55c57a; }
    
.search__form-input:focus:invalid {
    border-bottom: 3px solid #ff7730; }
.search__form-input::-webkit-input-placeholder {
    color: #999; }
    
.search__form-label {
    font-size: 1.2rem;
    font-weight: 700;
    margin-left: -4rem;
    margin-top: .7rem;
    display: block;
    -webkit-transition: all .3s;
    transition: all .3s; }
    
.search__form-input:placeholder-shown + .search__form-label {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(-4rem);
            transform: translateY(-4rem); }


.btn, .btn:link, .btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.5rem 4rem;
  display: inline-block;
  border-radius: 10rem;
  -webkit-transition: all .2s;
  transition: all .2s;
  position: relative;
  font-size: 1.6rem;
  border: none;
  cursor: pointer;
  margin-bottom: 5rem;
  }

.btn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2); }
  .btn:hover::after {
    -webkit-transform: scaleX(1.4) scaleY(1.6);
            transform: scaleX(1.4) scaleY(1.6);
    opacity: 0; }

.btn:active, .btn:focus {
  outline: none;
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2); }

.btn--white {
  background-color: #fff;
  color: #777; }
  .btn--white::after {
    background-color: #fff; }

.btn--green {
  background-color: #55c57a;
  color: #fff; }
  .btn--green::after {
    background-color: #55c57a; }

.btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 10rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-transition: all .4s;
  transition: all .4s; }

.btn--animated {
  -webkit-animation: moveInBottom .5s ease-out .75s;
          animation: moveInBottom .5s ease-out .75s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards; }

.btn-text:link, .btn-text:visited {
  font-size: 1.6rem;
  color: #55c57a;
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid #55c57a;
  padding: 3px;
  -webkit-transition: all .2s;
  transition: all .2s; }

.btn-text:hover {
  background-color: #55c57a;
  color: #fff;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px); }

.btn-text:active {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  -webkit-transform: translateY(0);
          transform: translateY(0); }

  @-webkit-keyframes moveInBottom {
      0% {
          opacity: 0;
          -webkit-transform: translateY(3rem);
                  transform: translateY(3rem); }
      100% {
          opacity: 1;
          -webkit-transform: translate(0);
                  transform: translate(0); } 
      }

  @keyframes moveInBottom {
      0% {
          opacity: 0;
          -webkit-transform: translateY(3rem);
                  transform: translateY(3rem); }
      100% {
          opacity: 1;
          -webkit-transform: translate(0);
                  transform: translate(0); } 
      }
.users {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
:root {
    --color-primary: #eb2f64;
    --color-green: #55c57a;
    --color-grey-dark-3: #999;
    --color-white-light-opaque: rgba(255, 255, 255, 0.6);
    --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, 0.3);
    --shadow-light: 0 2rem 5rem rgba(0, 0, 0, 0.06);
    --line: 1px solid var(--color-grey-light-2); 
}


.user {
    background-color: #fff;
    box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.06);
    box-shadow: var(--shadow-light);
    padding: 3rem;
    margin-bottom: 3.5rem;
    position: relative;
    overflow: hidden;
    -webkit-transform: skewX(-12deg);
            transform: skewX(-12deg); 
    margin: 0 auto;
    margin-bottom: 2rem;
    width: 75%;
    min-height: 23rem;
    font-size: 1.6rem;
    padding-left: 9rem;
    border: 1px solid #55c57a;
    border: 1px solid var(--color-green);
    border-radius: 3px;
}

.user__text {
    margin-bottom: 2rem;
    z-index: 10;
    position: relative; 
}
.user__user {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center; 
}
.user__photo {
    height: 8rem;
    width: 8rem;
    border-radius: 50%;
    margin-right: 1.5rem; 
}

.user__user-box {
    margin-right: auto; 
}

.user__name {
    font-size: 1.5rem;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: .4rem; 
}

.user__user-location {
    font-size: 1rem;
    color: #999;
    color: var(--color-grey-dark-3);
 }

.user__followers {
    color: #eb2f64;
    color: var(--color-primary);
    font-size: 2.2rem;
    font-weight: 300; 
}

.user__company-name {
    font-size: 1.2rem;
}

.user__username a:link,
.user__username a:visited,
.user__username a:hover,
.user__username a:active
.user__name a:link,
.user__name a:visited,
.user__name a:hover,
.user__name a:active
{
    text-decoration: none;
}

a {
    color: #333;
}

.loading {
    margin-top: 10rem;
    width: 4.8rem;
    height: 4.8rem;
    border: 4px solid #28b485;
    border-right-color: transparent;
    border-radius: 50%;
    -webkit-animation: rotate 1s infinite linear;
            animation: rotate 1s infinite linear;    
}

.loading__container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

@-webkit-keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.pagination {
    display: -webkit-flex;
    display: flex;
    margin: 5rem auto;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    -webkit-justify-content: center;
            justify-content: center;
   
  }
  .pagination__page-item.active .pagination__page-link {
    color: #777 !important;
    background-color: #55c57a !important;
    border-color: #55c57a !important;
  }
  .pagination__page-link {
    padding: 0.75rem 1rem;
    width: 7rem;
    text-align: center;
    box-shadow: none !important;
    border-color: #55c57a !important;
    color: #6b88a4;
    font-weight: 700;
    font-size: 1.4rem;
  }
  .pagination__page-link:hover {
    background-color: #ccc;
  }
  
